header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 3; }
.hamburger {
	display: none; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;

	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300;
		&:hover {
			text-decoration: none;
			color: $main-color; } }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		list-style-type: none;
		&:last-child {
			border-right: none; } } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);

	&_logo {
		max-width: 100%;
		margin-top: 16px; }

	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }

	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }

	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }

	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }


.promo {
	background: url('../img/bg/first_bg.jpg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }

	&_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	&_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }

	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }


.reasons {
	padding: 71px 0 65px 0;

	&_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }

	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $main-color;
		border-radius: 100%; }

	&_descr {
		width: 343px; }

	&_subtitle {
		line-height: 21px;
		color: $main-color;
		font-size: 17px;
		font-weight: 400; }

	&_text {
		color: #202020;
		font-size: 15px;
		font-weight: 300;
		line-height: 21px; } }

.label {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		background-color: #fff;
		color: #000; } }

.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	line-height: 1;
	z-index: 2;
	&_white {
		color: #ffffff; } }

.subtitle {
	position: relative;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	margin-top: 19px;
	z-index: 2;
	&_white {
		color: #fff; } }


.mobile {
	padding: 48px 0 80px;
	background: url('../img/bg/mobile_bg.jpg') center center/cover no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		&_1 {
			background: url('../img/photo/mobile_1.jpg') center center/cover no-repeat; }
		&_2 {
			background: url('../img/photo/mobile_2.jpg') center center/cover no-repeat; }
		&_3 {
			background: url('../img/photo/mobile_3.jpg') center center/cover no-repeat; }
		&_4 {
			background: url('../img/photo/mobile_4.jpg') center center/cover no-repeat; }
		&_5 {
			background: url('../img/photo/mobile_5.jpg') center center/cover no-repeat; }
		&_6 {
			background: url('../img/photo/mobile_6.jpg') center center/cover no-repeat; }
		&_subtitle {
			padding-top: 92px;
			text-align: center;
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-transform: uppercase; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			border-radius: 100%;
			cursor: pointer;
			&::before {
				content: ('');
				display: block;
				width: 14px;
				height: 2px;
				background-color: $main-color;
				transition: 0.3s; }
			&::after {
				content: ('');
				display: block;
				width: 2px;
				height: 14px;
				background-color: $main-color;
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				transition: 0.3s; }
			&:hover {
				&::before, &::after {
					background-color: $second_color; } } } } }

.choice {
	position: relative;
	padding: 57px 0 138px;
	background: url('../img/bg/mobile_bg.jpg') center center/cover no-repeat;
	.black {
		height: 499px;
		width: 100%;
		background-color: #070716;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1; }
	&_car {
		position: relative;
		display: block;
		margin: 56px auto 0;
		z-index: 2; }
	&_descr {
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		text-align: center; } }

.require {
	padding: 68px 0 84px;
	& .title {
		margin-bottom: 33px; }
	&_header {
		color: $second_color;
		font-size: 22px;
		font-weight: 400;
		line-height: 1;
		margin-bottom: 15px; }
	&_list {
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		list-style-type: none;
		li {
			position: relative;
			margin-bottom: 10px;
			&::before {
				content: ('');
				display: block;
				position: absolute;
				left: -32px;
				top: 2px;
				width: 21px;
				height: 20px;
				background: url('../img/logo/completed_r.png') center center/cover no-repeat; } } }
	&_descr {
		font-size: 17px;
		font-weight: 300;
		color: $main_color; }
	&_block {
		margin-bottom: 62px;
		& .require_header {
			color: $main_color; }
		& li::before {
			background: none;
			background: url('../img/logo/completed_b.png') center center/cover no-repeat; } } }

.world {
	position: relative;
	background: url('../img/bg/last_bg.jpg')center center/cover no-repeat;
	min-height: 717px;
	padding: 49px 0;
	&_link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		color: #1fbad6;
		font-size: 15px;
		font-weight: 400;
		text-transform: uppercase; } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px;
		&_div {
			width: 33%;
			&:nth-child(3) {
				display: flex;
				justify-content: flex-end; } } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } } } }

.xs-visible {
	display: none; }
