@media (max-width:1200px) {
	.menu {
		&_item {
			padding-right: 20px; }
		&_link {
			font-size: 13px; } }
	.promo {
		&_header {
			font-size: 52px;
			line-height: 55px; } }
	.reasons {
		&_descr {
			width: 310px;
			&_block {
				align-items: flex-start; } } }
	.mobile {
		&_item {
			width: 300px; } }
	.footer {
		&_links {
			width: 100%;
			&_main a {
				font-size: 14px; }
			&_sub a {
				font-size: 10px; } }
		&_mobile {
			&_img {
				width: 250px; } } } }

@media (max-width:991px) {
	.title {
		font-size: 33px; }
	.subtitle {
		font-size: 15px; }
	.menu {
		&_item {
			padding-right: 9px; }
		&_link {
			font-size: 10px; } }
	.subheader {
		&_official {
			font-size: 11px; }
		&_phone {
			font-size: 19px; }
		&_btn {
			margin-top: 24px;
			width: 100%;
			height: 35px;
			font-size: 13px; } }
	.promo {
		&_header {
			font-size: 41px;
			line-height: 46px; }
		&_subheader {
			font-size: 30px; } }
	.reasons {
		&_block {
			flex-direction: column; }
		&_descr {
			margin-top: 20px; } }
	.mobile {
		&_item {
			width: 220px;
			height: 235px;
			&_subtitle {
				padding-top: 70px;
				font-size: 18px; } } }
	.choice {
		&_car {
			width: 550px;
			margin-top: 100px; } }
	.require {
		&_list {
			font-size: 15px;
			li {
				margin-bottom: 8px; } } }
	.footer {
		&_social {
			width: 150px;
			&_item {
				margin: 0; } }
		&_links {
			&_main {
				a {
					font-size: 11px; } }
			&_sub {
				a {
					font-size: 8px; } } }
		&_mobile {
			&_img {
				width: 190px; } } } }
@media (max-width:767px) {
	.menu {
		padding: 0;
		position: fixed;
		left: -100%;
		top: 0;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 50%;
		height: 100vh;
		background-color: rgba(0,0,0, 0.8);
		transition: 0.6s all;
		&_active {
			left: 0; }
		&_item {
			border: none;
			padding-right: 0;
			margin-top: 20px; }
		&_link {
			font-size: 20px; } }
	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		width: 26px;
		height: 12px;
		span {
			display: block;
			width: 100%;
			height: 2px;
			margin-bottom: 2px;
			background-color: #fff;
			transition: 0.5s all; }
		&_active {
			span {
				margin-bottom: -2px;
				&:nth-child(1) {
					transform: translateY(3px) rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: translateY(3px) rotate(45deg); } } } }
	.xs-hidden {
		display: none; }
	.xs-visible {
		display: block; }
	.subheader {
		&_official {
			display: none; }
		&_phone {
			text-align: center;
			display: block;
			margin-top: 10px;
			color: #ffffff;
			font-size: 16px;
			font-weight: 400;
			text-transform: uppercase; }
		&_btn {
			display: block;
			width: 150px;
			height: 40px;
			color: #ffffff;
			font-size: 10px;
			text-transform: uppercase;
			margin: 0 auto;
			margin-top: 10px; } }
	.mobile {
		&_item {
			width: 100%; } }
	.reasons {
		&_block {
			display: flex;
			flex-direction: row; } }
	.choice {
		&_car {
			width: 500px; } }
	.world {
		min-height: 600px; }
	footer {
		padding-bottom: 10px; }
	.footer {
		&_social {
			width: 153px;
			margin: 0 auto; }
		&_wrapper {
			display: block;
			padding-top: 20px;
			&_div {
				margin: 0 auto;
				margin-bottom: 10px;
				width: 280px;
				&:nth-child(3) {
					display: flex;
					justify-content: center; } } }
		&_links {
			&_sub {
				margin-top: 5px; } } } }

@media (max-width:575px) {
	.menu {
		&_active {
			width: 75%; }
		&_item {
			width: 100%;
			text-align: center; }
		&_link {
			display: block; } }
	.title {
		font-size: 15px;
		font-weight: 700;
		margin-top: 16px; }
	.subtitle {
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		margin-top: 8px; }
	header {
		height: 30px; }
	nav {
		height: 30px; }
	.promo {
		min-height: 340px;
		padding: 90px 0 35px 0;
		&_header {
			font-size: 30px;
			font-weight: 700;
			line-height: 35px; }
		&_subheader {
			font-size: 20px;
			font-weight: 400;
			line-height: 25px; }
		&_descr {
			display: none; }
		&_btn {
			margin-top: 72px;
			width: 210px;
			height: 50px;
			&:hover {
				background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }
	.subheader {
		height: 60px;
		top: 30px;
		&_btn {
			width: 120px;
			height: 23px;
			font-size: 11px;
			margin-top: 7px;
			&:hover {
				background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } }
		&_logo {
			margin-top: 20px; } }
	.reasons {
		padding: 23px 0 50px 0;
		&_block {
			display: flex;
			flex-direction: column;
			margin-top: 26px; }
		&_round {
			margin: 0 auto; } }
	.mobile {
		padding: 23px 0 34px;
		&_item {
			width: 290px;
			height: 254px;
			&_plus {
				bottom: 28px;
				&:hover {
					&:before, &:after {
						background-color: $main-color; } } } } }
	.choice {
		min-height: 390px;
		background: #070716;
		padding: 25px 0 33px;
		&_car {
			width: 300px;
			margin-top: 35px; }
		&_descr {
			color: #ffffff; } }
	.black {
		display: none; }
	.require {
		padding: 25px 0;
		.title {
			margin-bottom: 20px; }
		&_block {
			margin-bottom: 31px; }
		&_red {
			margin-bottom: 31px; } }
	.world {
		padding: 25px 0 30px;
		min-height: 360px;
		&_link {
			font-size: 12px; } }
	.footer {
		&_divider {
			width: 280px; } } }





